import { useState } from 'react';
import "./classbooking.css";
export function ClassBooking() {
  const [bookingValues, setBookingValues] = useState({
    date: '',
    city: '',
    classId: '',
  });

  const [classList, setClassList] = useState([]);

  const handleInputChange = (event) => {
    const { name, value } = event.target;
    setBookingValues({ ...bookingValues, [name]: value });
  };

  const handleGoClick = async () => {
    const response = await fetch(`http://202backend-env.eba-rgmq4hxp.us-west-1.elasticbeanstalk.com/classSchedule?date=${bookingValues.date}&city=${bookingValues.city}`);
    if (response.ok) {
      const data = await response.json();
      setClassList(data);
    } else {
      alert('Failed to fetch class list. Please try again later.');
    }
  };

  const handleBookClick = async (membername, classname, gymaddress, city, starttime, endtime, date) => {
    const response = await fetch('http://202backend-env.eba-rgmq4hxp.us-west-1.elasticbeanstalk.com/bookclass', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({membername, classname, gymaddress, city, starttime, endtime, date }),
    });
    if (response.ok) {
      alert('Booking successful!');
    } else {
      alert('Booking failed. Please try again later.');
    }
  };
  

  return (
    <div className='form' style={{width:"70%", textAlign:"center"}}>
      <div>
        <label >Date:</label>
        <input type="date" name="date" value={bookingValues.date} onChange={handleInputChange} />
      </div>
      <div>
        <label>City:</label>
        <select name="city" value={bookingValues.city} onChange={handleInputChange}>
          <option value="">Select City</option>
          <option value="Columbus">Columbus</option>
          <option value="San Jose">San Jose</option>
          <option value="Seattle">Seattle</option>
          <option value="Richardson">Richardson</option>
          <option value="Orlando">Orlando</option>
        </select>
      </div>
      <button onClick={handleGoClick}>Go</button>

      <div>
        {classList.length > 0 && (
        <table>
          <thead>
            <tr>
              <th>Class Name</th>
              <th>Gym Address</th>
              <th>City</th>
              <th>Start Time</th>
              <th>End Time</th>
              <th>Date</th>
              <th>Book</th>
            </tr>
          </thead>
          <tbody>
            {classList.map((classItem) => (
              <tr key={classItem.classId}>
                <td>{classItem.classname}</td>
                <td>{classItem.gymaddress}</td>
                <td>{classItem.city}</td>
                <td>{classItem.starttime}</td>
                <td>{classItem.endtime}</td>
                <td>{classItem.date}</td>
                <td><button onClick={() => handleBookClick(localStorage.getItem("name"), classItem.classname, classItem.gymaddress, classItem.city, classItem.starttime, classItem.endtime, classItem.date)}>Book</button></td>
              </tr>
            ))}
          </tbody>
        </table>)}
      </div>
    </div>
  );
}
