import { useState } from 'react';

function AddEmployeePage() {
  const [formValues, setFormValues] = useState({
    employeename: '',
    age: '',
    address: '',
    phno: '',
    designation: '',
    gymaddress: '',
    city: '',
    password: '',
  });

  const handleInputChange = (event) => {
    const { name, value } = event.target;
    setFormValues({ ...formValues, [name]: value });
  };

  const handleEnrollClick = async () => {
    const response = await fetch('http://202backend-env.eba-rgmq4hxp.us-west-1.elasticbeanstalk.com/addemployee', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(formValues),
    });
    if (response.ok) {
      alert('Enrollment successful!');
      setFormValues({
        employeename: '',
        age: '',
        address: '',
        phno: '',
        designation: '',
        gymaddress: '',
        city: '',
        password: '',
      });
    } else {
      alert('Enrollment failed. Please try again later.');
    }
  };

  return (
    <div className='form'>
      <div className='form_item'>
        <label className='form_item_label'>Employee Name:</label>
        <input className='form_item_input' type="text" name="employeename" value={formValues.employeename} onChange={handleInputChange} />
      </div>
      <div className='form_item'>
        <label className='form_item_label'>Age:</label>
        <input className='form_item_input' type="text" name="age" value={formValues.age} onChange={handleInputChange} />
      </div>
      <div  className='form_item'>
        <label className='form_item_label'>Address:</label>
        <input className='form_item_input' type="text" name="address" value={formValues.address} onChange={handleInputChange} />
      </div>
      <div className='form_item'>
        <label className='form_item_label'>Phone Number:</label>
        <input className='form_item_input' type="text" name="phno" value={formValues.phno} onChange={handleInputChange} />
      </div>
      <div className='form_item'>
        <label className='form_item_label'>Designation:</label>
        <input className='form_item_input' type="text" name="designation" value={formValues.designation} onChange={handleInputChange} />
      </div>
      <div className='form_item'>
        <label className='form_item_label'>Gym Address:</label>
        <input className='form_item_input' type="text" name="gymaddress" value={formValues.gymaddress} onChange={handleInputChange} />
      </div>
      <div className='form_item'>
        <label className='form_item_label'>City:</label>
        <input className='form_item_input' type="text" name="city" value={formValues.city} onChange={handleInputChange} />
      </div>
      <div className='form_item'>
        <label className='form_item_label'>Password:</label>
        <input className='form_item_input' type="password" name="password" value={formValues.password} onChange={handleInputChange} />
      </div>
      <div style={{width:"100%", display:"flex", justifyContent:"center"}}><button className="form_button" style={{width:"100%", display:"flex", justifyContent:"center"}} onClick={handleEnrollClick}>Add Employee</button></div>
    </div>
  );
}

export default AddEmployeePage;
