import { useState } from 'react';

export function AddClasses() {
  const [formValues, setFormValues] = useState({
    classname: '',
    starttime: '',
    endtime: '',
    gymaddress: '',
    city: '',
    date: '',
  });

  const handleInputChange = (event) => {
    const { name, value } = event.target;
    setFormValues({ ...formValues, [name]: value });
  };

  const handleEnrollClick = async () => {
    const response = await fetch('http://202backend-env.eba-rgmq4hxp.us-west-1.elasticbeanstalk.com/addclasses', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(formValues),
    });
    if (response.ok) {
      alert('Class added successfully!');
      setFormValues({
        classname: '',
        starttime: '',
        endtime: '',
        gymaddress: '',
        city: '',
        date: '',
      });
    } else {
      alert('Failed. Please try again later.');
    }
  };

  return (
    <div className='form'>
      <div className='form_item'>
        <label className='form_item_label'>Class Name:</label>
        <input className='form_item_input' type="text" name="classname" value={formValues.classname} onChange={handleInputChange} />
      </div>
      <div className='form_item'>
        <label className='form_item_label'>Start Time:</label>
        <input className='form_item_input' type="time" name="starttime" value={formValues.starttime} onChange={handleInputChange} />
      </div>
      <div className='form_item'>
        <label className='form_item_label'>End Time:</label>
        <input className='form_item_input' type="time" name="endtime" value={formValues.endtime} onChange={handleInputChange} />
      </div>
      <div className='form_item'>
        <label className='form_item_label'>Gym Address:</label>
        <input className='form_item_input' type="text" name="gymaddress" value={formValues.gymaddress} onChange={handleInputChange} />
      </div>
      <div className='form_item'>
        <label className='form_item_label'>City:</label>
        <input className='form_item_input' type="text" name="city" value={formValues.city} onChange={handleInputChange} />
      </div>
      <div className='form_item'>
        <label className='form_item_label'>Date:</label>
        <input className='form_item_input' type="date" name="date" value={formValues.date} onChange={handleInputChange} />
      </div>
      <div style={{width:"100%", display:"flex", justifyContent:"center"}}><button className="form_button" onClick={handleEnrollClick}>Add Class</button></div>
    </div>
  );
}
